  body {
    margin: 0;
    font-family: "Montserrat", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }

  /* EDITBOX --------------- */
  .editbox {
    margin-bottom: 1rem;
  }
  .editbox textarea {
    font-family: inherit;
  }

  .editbox .edit-action {
    text-decoration: underline;
    font-size: 1rem;
    font-weight: normal;
  }

  .editbox .editbox-textarea {
    outline: none;
    border: none;
    color: var(--text-secondary);
    width: 100%;
    resize: none;
    background-color: transparent;
    border: 1px solid #c6cbd4;
    border-radius: 0.5rem;
    padding: 1rem;
    transition: 0.2s;
  }

  .editbox .editbox-textarea:hover {
    border: #9090cc 0.5px solid;
  }

  .editbox .editbox-textarea:focus,
  :active,
  :visited {
    outline: none;
  }

  .editbox .text-edit {
    color: var(--text-secondary);
    background-color: transparent;
    font-weight: normal;
  }

  .editbox .edit-bottom {
    transform: translate(-0.5rem, -1.5rem);
  }

  .modal {
    width: 500px;
    background: white;
    border: 1px solid #ccc;
    transition: 1.1s ease-out;
    box-shadow: -2rem 2rem 2rem rgba(black, 0.2);
    filter: blur(0);
    transform: scale(1);
    opacity: 1;
    visibility: visible;
    margin: auto;
  }

  h2 {
    border-bottom: 1px solid #ccc;
    padding: 1rem;
    margin: 0;
  }

  .content {
    padding: 1rem;
  }

  .actions {
    border-top: 1px solid #ccc;
    background: #eee;
    padding: 0.5rem 1rem;
  }

  button {
    border: 0;
    background: #78f89f;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    line-height: 1;
  }
